<template>
  <div id="konvacanvas"></div>
</template>

<script>
import Konva from 'konva'

export default {
  props: {
    colors: Array,
    rings: Number,
    strokeWidth: Number,
    speed: Number,
    running: Boolean
  },
  data: function () {
    return {
      parent: document.getElementById('content'),
      dimension: {
        width: 0,
        height: 0
      },
      params: {
        size: 0,
        circles: 0
      },
      circles: [],
      status: {
        init: false,
        running: false,
        delay: null
      },
      konva: {
        stage: null,
        layer: null,
        animation: null
      }
    }
  },
  methods: {
    debounce: function debounce (func, timeout = 500) {
      let timer
      return (...args) => {
        clearTimeout(timer)
        timer = setTimeout(() => { func.apply(this, args) }, timeout)
      }
    },
    getColor: function (n) {
      return this.colors[n % 3]
    },
    setParams: function () {
      this.params.size = Math.max(this.dimension.width, this.dimension.height) / (this.rings * 2)
      const hyponetuse = Math.sqrt((this.dimension.width / 2) * (this.dimension.width / 2) + (this.dimension.height / 2) * (this.dimension.height / 2))
      this.params.circles = Math.ceil((hyponetuse / this.params.size) / 3) * 3 + 3
    },
    setDimension: function () {
      if (this.parent) {
        this.dimension = {
          width: this.parent.clientWidth,
          height: this.parent.clientHeight
        }
        if (this.konva.stage) {
          this.konva.stage.width(this.dimension.width)
          this.konva.stage.height(this.dimension.height)
        }
      }
    },
    createCircles: function () {
      this.konva.layer.destroyChildren()
      let n = 0
      this.circles = []
      const x = this.dimension.width / 2
      const y = this.dimension.height / 2
      while (n < this.params.circles) {
        const circle = new Konva.Circle({
          x: x,
          y: y,
          fill: this.getColor(n),
          stroke: 'black',
          radius: n * this.params.size,
          strokeWidth: this.strokeWidth,
          hitStrokeWidth: 0,
          shadowForStrokeEnabled: false,
          listening: false
        })
        if (n < 1) {
          circle.radius(4)
          circle.strokeWidth(8)
        }
        this.circles.push(circle)
        n = n + 1
      }
      for (let index = this.circles.length - 1; index >= 0; index--) {
        this.konva.layer.add(this.circles[index])
      }
      this.konva.stage.draw()
    },
    iniKonva: function () {
      this.konva.stage = new Konva.Stage({
        container: 'konvacanvas',
        width: this.dimension.width,
        height: this.dimension.height
      })
      this.konva.layer = new Konva.Layer()
      this.konva.stage.add(this.konva.layer)
      this.konva.animation = new Konva.Animation((frame) => {
        const currentSpeed = this.speed
        for (let index = 0; index < this.circles.length; index++) {
          const circle = this.circles[index]
          const currentRadius = circle.radius()
          if (currentRadius > this.params.circles * this.params.size) {
            circle.radius(2)
            circle.strokeWidth(4)
            circle.moveToTop()
          } else {
            const newRadius = currentRadius + currentSpeed * (frame.timeDiff / 1000)
            if (circle.strokeWidth() < this.strokeWidth) circle.strokeWidth(Math.min(newRadius * 2, this.strokeWidth))
            circle.radius(newRadius)
          }
        }
      }, this.konva.layer)
    },
    startAnimation: function () {
      this.konva.animation.start()
      this.status.running = true
    },
    stopAnimation: function () {
      this.konva.animation.stop()
      this.status.running = false
    }
  },
  watch: {
    rings: function () {
      if (this.status.running) this.stopAnimation()
      this.setParams()
      this.createCircles()
      if (this.running) this.startAnimation()
    },
    strokeWidth: function (newStrokeWidth) {
      if (!this.status.init) return
      for (let index = 0; index < this.circles.length; index++) {
        const circle = this.circles[index]
        circle.strokeWidth(Math.min(circle.radius() * 2, newStrokeWidth))
      }
      if (!this.status.running) this.konva.stage.draw()
    },
    colors: {
      immediate: true,
      handler: function () {
        if (!this.status.init) return
        for (let index = 0; index < this.circles.length; index++) {
          const circle = this.circles[index]
          circle.fill(this.getColor(index))
        }
        if (!this.status.running) this.konva.stage.draw()
      }
    },
    running: function () {
      if (!this.status.init) return
      if (this.running) {
        this.startAnimation()
      } else {
        this.stopAnimation()
      }
    }
  },
  mounted: function () {
    this.parent = document.getElementById('content')
    window.addEventListener('resize', () => {
      if (this.status.running) this.stopAnimation()
      this.setDimension()
      this.setParams()
      this.createCircles()
      clearTimeout(this.status.delay)
      this.status.delay = setTimeout(() => { if (this.running) this.startAnimation() }, 1000)
    })
    this.setDimension()
    this.setParams()
    this.iniKonva()
    this.createCircles()
    this.status.init = true
  }
}
</script>

