<template>
  <div id="app" class="is-flex is-flex-direction-column">
    <div id="head" class="is-flex is-flex-direction-row is-align-items-center">
      <b-dropdown append-to-body aria-role="list" trap-focus :mobile-modal="false">
        <template #trigger>
            <b-button icon-right="sliders-h" />
        </template>
        <b-dropdown-item aria-role="listitem" :focusable="false" custom>
          <div class="is-flex is-flex-direction-row is-align-items-center">
            <div class="setting-label has-text-weight-bold">Colors</div>
            <div class="mr-1"><input type="color" name="color1" :value=colors[0] @change="onColorChange(0, $event)"></div>
            <div class="mr-1"><input type="color" name="color1" :value=colors[1] @change="onColorChange(1, $event)"></div>
            <div><input type="color" name="color1" :value=colors[2] @change="onColorChange(2, $event)"></div>
          </div>
          <div class="mt-2 is-flex is-flex-direction-row is-align-items-center">
            <div class="setting-label has-text-weight-bold">Rings</div>
            <div class="is-flex-grow-1"><b-numberinput expanded size="is-small" type="is-dark" controls-position="compact" min="1" max="10" v-model="rings"></b-numberinput></div>
          </div>
          <div class="mt-2 is-flex is-flex-direction-row is-align-items-center">
            <div class="setting-label has-text-weight-bold">Stroke</div>
            <div class="is-flex-grow-1"><b-numberinput expanded size="is-small" type="is-dark" controls-position="compact" min="5" max="30" v-model="strokeWidth"></b-numberinput></div>
          </div>
          <div class="mt-2 is-flex is-flex-direction-row is-align-items-center">
            <div class="setting-label has-text-weight-bold">Speed</div>
            <div class="is-flex-grow-1"><b-numberinput expanded size="is-small" type="is-dark" controls-position="compact" min="10" max="200" step="10" v-model="speed"></b-numberinput></div>
          </div>
        </b-dropdown-item>
      </b-dropdown>

      <div class="ml-5 mr-3 mb-0 field has-addons">
        <p class="control"><b-button icon-left="play" :disabled="running" @click="onPlay" /></p>
        <p class="control"><b-button icon-left="stop" :disabled="!running" @click="onStop" /></p>
      </div>

      <b-dropdown append-to-body aria-role="list" trap-focus :mobile-modal="false">
        <template #trigger>
            <b-button :icon-right="volumeIcon" />
        </template>
        <b-dropdown-item aria-role="listitem" :focusable="false" custom>
          <b-slider type="is-dark" v-model="volume" :min="0" :max="1" :step="0.05" :indicator="false" :tooltip="false"></b-slider>
        </b-dropdown-item>
      </b-dropdown>

      <div class="is-flex-grow-1"></div>

      <div class="ml-5">
        <b-button icon-right="link" :type="copied ? 'is-success' : ''"  @click="onCopyToClipboard" />
      </div>

      <div class="ml-3">
        <b-button icon-right="info" @click="showInfo = !showInfo" />
      </div>

    </div>
    <div id="content" class="is-flex-grow-1">
      <konva-canavas :colors="colors" :rings="rings" :stroke-width="strokeWidth" :speed="speed" :running="running"></konva-canavas>
    </div>

    <info-modal v-bind:showInfo.sync="showInfo"></info-modal>
  </div>
</template>

<script>
import KonvaCanavas from './components/KonvaCanvas.vue'
import InfoModal from './components/InfoModal.vue'

export default {
  name: 'App',
  components: {
    KonvaCanavas,
    InfoModal
  },
  data: function () {
    return {
      colors: ['#7e99f9', '#5fbaba', '#d3bf2c'],
      rings: 3,
      strokeWidth: 15,
      speed: 100,
      running: false,
      audio: null,
      volume: 0,
      muted: false,
      showInfo: false,
      copied: null
    }
  },
  methods: {
    onColorChange: function (index, event) {
      this.$set(this.colors, index, event.target.value)
    },
    onPlay: function () {
      this.running = true
      if (this.audio) this.audio.play()
    },
    onStop: function () {
      this.running = false
      if (this.audio) {
        this.audio.pause()
        this.audio.currentTime = 0
      }
    },
    onCopyToClipboard: async function () {
      try {
        const url = `${window.location.origin}?c1=${this.colors[0].slice(1)}&c2=${this.colors[1].slice(1)}&c3=${this.colors[2].slice(1)}&r=${this.rings}&st=${this.strokeWidth}&sp=${this.speed}`
        await navigator.clipboard.writeText(url)
        clearTimeout(this.copied)
        this.copied = setTimeout(() => {
          this.copied = null
        }, 300)
      } catch (error) {
        console.error(error)
      }
    }
  },
  watch: {
    volume: function (newVolume) {
      this.audio.volume = newVolume
    }
  },
  computed: {
    volumeIcon: function () {
      if (this.volume === 0) {
        return 'volume-mute'
      } else if (this.volume < 0.6) {
        return 'volume-down'
      } else {
        return 'volume-up'
      }
    }
  },
  created: function () {
    const urlParams = new URLSearchParams(window.location.search)
    if (urlParams.get('c1')) this.colors[0] = '#' + urlParams.get('c1')
    if (urlParams.get('c2')) this.colors[1] = '#' + urlParams.get('c2')
    if (urlParams.get('c3')) this.colors[2] = '#' + urlParams.get('c3')
    if (urlParams.get('r')) this.rings = Math.min(10, Math.max(1, parseInt(urlParams.get('r'))))
    if (urlParams.get('st')) this.strokeWidth = Math.min(30, Math.max(5, parseInt(urlParams.get('st'))))
    if (urlParams.get('sp')) this.speed = Math.min(200, Math.max(10, parseInt(urlParams.get('sp'))))
  },
  mounted: function () {
    this.audio = new Audio()
    this.audio.preload = 'auto'
    this.audio.volume = this.volume
    this.audio.loop = true
    this.audio.muted = this.muted
    this.audio.src = require('../src/assets/asmr.mp3')
  }
}
</script>

<style lang="scss">
#app {
  height: 100%;
}
#head {
  height: 60px;
  min-height: 60px;
  background-color: #363636;
  padding: 0 15px;
}
.setting-label {
  width: 55px;
  min-width: 55px;
}
#content {
  overflow: hidden;
}
</style>
